<template>
  <el-dialog
    title=""
    :visible.sync="dialogVisible"
    width="10%"
    :before-close="handleClose"
  >
    <div class="register-wrapper">
      <div id="register">
        <p class="title"></p>
        <el-form
          :model="ruleForm2"
          status-icon
          :rules="rules2"
          ref="ruleForm2"
          label-width="0"
          class="demo-ruleForm"
        >
          <el-form-item prop="username">
            <el-input
              v-model="ruleForm2.username"
              auto-complete="off"
              placeholder=""
            ></el-input>
          </el-form-item>
          <el-form-item prop="password">
            <el-input
              ref="formName"
              v-model="ruleForm2.password"
              auto-complete="off"
              placeholder=""
            ></el-input>
          </el-form-item>

          <el-form-item>
              
            <el-select v-model="ruleForm2.college" style="width:100%" placeholder="">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
         
          <el-form-item prop="grade">
              
            <el-select v-model="ruleForm2.grade"  style="width:100%" placeholder="">
              <el-option :label="16" :value="16"> </el-option>
              <el-option :label="17" :value="17"> </el-option>
              <el-option :label="18" :value="18"> </el-option>
              <el-option :label="19" :value="19"> </el-option>
              <el-option :label="20" :value="20"> </el-option>
            </el-select>
          </el-form-item>
          <el-form-item prop="phone">
            <el-input
              v-model="ruleForm2.phone"
              placeholder=""
            ></el-input>
          </el-form-item>

          <el-form-item>
            <el-button @click="cancel"> </el-button>
            <el-button type="primary" @click="submitForm('ruleForm2')"
              ></el-button
            >
          </el-form-item>
        </el-form>
      </div>
    </div>
  </el-dialog>
</template>

<script>

export default {
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.ruleForm2.checkPass !== "") {
          this.$refs.ruleForm2.validateField("checkPass");
          
        }
        callback();
      }
    };
    return {
      //
      autoArr:'',
      dialogVisible: false,
      ruleForm2: {
        username: "",
        password: "",
        checkPass: "",
        college: "",
        grade: 16,
        phone: ""
      },

      rules2: {
        pass: [{ validator: validatePass, trigger: "change" }],
        username: [
          { require: true, message: "", trigger: "blur" },
          { min: 4, max: 12, message: "412", trigger: "blur" }
        ],
        password: [
          { require: true, message: "", trigger: "blur" },
          { min: 6, max: 12, message: "624", trigger: "blur" }
        ],
        college: [
          { require: true, message: "", trigger: "blur" }
        ],
        phone:[
          { require: true, message: "", trigger: "blur" }
        ]
      },
      flag: true,
      options: [
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        },
        {
          value: "",
          label: ""
        }
      ]
    };
  },
  created(){
    this.dialogVisible = true
  },
  methods: {
    // addUserconfirm(){
    //     this.dialogVisible = false
    //     setTimeout(() => {
    //   this.$router.push('/userInfo')
    // }, 300)
    // },
    handleClose(done) {
      this.$confirm("")
        .then(_ => {
          done();
        })
        .catch(_ => {});
    },
    submitForm(formName) {
    if (this.check()) {
      this.$refs[formName].validate(async valid => {
        if (valid) {
          const { data: res } = await this.$http.post(
            "/register",
            this.ruleForm2
          );
          console.log(res);
          if (res.status == "0") {
            setTimeout(() => {
              this.$message.success("");
              this.$router.push({
                path: "/userInfo"
              });
            }, 300);
          } else {
            this.$message(res.message);
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    }
  },

  check() {
    if (!/^1[3456789]\d{9}$/.test(this.ruleForm2.phone)) {
      this.$message.error("");
      return false;
    } else  if(this.ruleForm2.username == '' || this.ruleForm2.username == null){
      this.$message.error("")
       return false
  }else if(this.ruleForm2.password == '' || this.ruleForm2.password == null){
      this.$message.error("")
       return false
  }else if(this.ruleForm2.college == '' || this.ruleForm2.college == null){
      this.$message.error("")
       return false
  }else if(this.ruleForm2.grade == '' || this.ruleForm2.grade == null){
      this.$message.error("")
       return false
  }else{
      return true
  }
  },
  cancel(){
      this.dialogVisible  = false
      setTimeout(()=>{
          this.$router.push('/userInfo')
      },300)
  },

  //
  async getAutoArr(){
    const { data: res } = await this.$http.get('/getAuthority', {
      params: {id:window.sessionStorage.getItem('id')}
    })
    this.autoArr = res;
    console.log(res)
  }


  }
};

</script>

<style scoped>
.loading-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background: #aedff8;
  display: flex;
  align-items: center;
  justify-content: center;
}
.register-wrapper img {
  position: absolute;
  z-index: 1;
}
.register-wrapper {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}
#register {
  max-width: 340px;
  margin: 60px auto;
  background: #fff;
  padding: 20px 40px;
  border-radius: 10px;
  position: relative;
  z-index: 9;
}
.title {
  font-size: 26px;
  line-height: 50px;
  font-weight: bold;
  margin: 10px;
  text-align: center;
}
.el-form-item {
  text-align: center;
}
.login {
  margin-top: 10px;
  font-size: 14px;
  line-height: 22px;
  color: #1ab2ff;
  cursor: pointer;
  text-align: left;
  text-indent: 8px;
  width: 160px;
}
.login:hover {
  color: #2c2fd6;
}
.code >>> .el-form-item__content {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.code button {
  margin-left: 20px;
  width: 140px;
  text-align: center;
}
.el-button--primary:focus {
  background: #409eff;
  border-color: #409eff;
  color: #fff;
}
</style>
