<template>
  <div>
    <!-- <v-editUser
      v-if="edit.showEdit"
      :user="edit.user"
      @notShowEdit="notShow"
    ></v-editUser>
    <router-view></router-view> -->
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- gutter  -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder=""
            v-model="query"
            class="input-with-select"
          >
            <el-button
              :disabled="disabled.select"
              slot="append"
              icon="el-icon-search"
              @click="selectAllFoodRecord"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <router-link to="/addUser"
            ><el-button :disabled="disabled.add" type="primary"
              ></el-button
            ></router-link
          >
        </el-col>
      </el-row>

      <!--  -->
       <el-table
                  class="table"
                  border
                  stripe
                  :data="allFoodRecord"
                  style="width: 100%;"
                >
                  <el-table-column type="index"></el-table-column>
                  <el-table-column prop="id" label="ID">
                  </el-table-column>
                  <el-table-column prop="subject" label="">
                  </el-table-column>
                  <el-table-column prop="total_amount" label="">
                  </el-table-column>
                  <el-table-column prop="product_code" label="ID">
                  </el-table-column>
                  <el-table-column prop="body" label="">
                  </el-table-column>
                  <el-table-column prop="username" label="">
                  </el-table-column>
                  <el-table-column prop="userid" label="ID">
                  </el-table-column>
                  <el-table-column prop="out_trade_no" label="ID">
                  </el-table-column>
                  <el-table-column prop="allneedtime" label="()">
                  </el-table-column>
                  <el-table-column  label="">
                     <template slot-scope="scope">
                        <i class="el-icon-success" style="color:lightgreen" v-if="scope.row.dispose === true"></i>
                        <i class="el-icon-error" style="color:red" v-else></i>
                     </template>
                  </el-table-column>
                  <el-table-column label="">
                    <template slot-scope="scope">
                      {{ scope.row.date | dateFormat }}
                    </template>
                  </el-table-column>
                  <el-table-column label="">
                      <template slot-scope="scope">
                     <el-button v-if="scope.row.dispose == false" type="success" size="mini" @click="disposeFoodRecord(scope.row)"></el-button>
                    </template>
                    
                  </el-table-column>
                </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  
  </div>
</template>
<script>
export default {
  data() {
    return {
      //
      query: "",
      //
      queryInfo: {
        pageNum: 1,
        pageSize: 5
      },
      total: 0,
      //
      disabled: {
        add: true,
        delete: true,
        update: true,
        select: true,
        detail: true
      },
      allFoodRecord: [],
      dialogVisible: false,

    };
  },
  created() {
    // this.selectAllRecord();
    this.getAutoArr();
  },
  methods: {
    async selectAllFoodRecord() {
      const { data: res } = await this.$http.post(
        `/selectAllFoodRecord/${this.query}`,
        this.queryInfo
      );
      console.log(res);
      console.log("124567897946");
      console.log(res);

        for (let index = 0; index < res.length; index++) {
              res[index].indate = this.timestampToTime (res[index].indate)
          }
      this.allFoodRecord = res.list;
      this.total = res.num;
    },
  
 
    handleClose() {},
    // pageSize
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pageSize = newSize;
      this.queryInfo.pageNum = 1;
      this.selectAllFoodRecord();
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage;
      console.log(newPage);
      this.selectAllFoodRecord();
    },
    async getAutoArr() {
      const { data: res } = await this.$http.get("/getAuthorityString", {
        params: { id: window.sessionStorage.getItem("id") }
      });
      this.autoArr = res;
      console.log(res);
      var strs = new Array(); //
      strs = res.split(","); //
      for (var i = 0; i < strs.length; i++) {
        if (strs[i] == "101") {
          this.disabled.select = false;
        } else if (strs[i] == "404") {
          this.disabled.update = false;
        } else if (strs[i] == "403") {
          this.disabled.add = false;
        } else if (strs[i] == "401") {
          this.disabled.delete = false;
        } else if (strs[i] == "405") {
          this.disabled.detail = false;
        }
      }
    },
    //
    disposeFoodRecord(value){
      this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          console.log(value)
          const { data: res } = await this.$http.post(`/disposeFoodRecord/${value.id}/${value.userid}`, {})
          console.log(res)
          if(res.status === 0){
              this.$message({
            type: 'success',
            message: '!'
          });
          }else{
            this.$message.error('')
          }
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });          
        });

    }
  }
};
</script>
<style scoped></style>
